
.loader {
    border: 4px solid rgba(242, 234, 234, 0.1);
    border-left-color: transparent;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    animation: spin89345 1s linear infinite;
}

@keyframes spin89345 {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}